@use 'base/variables' as v;
@use 'base/mixins' as m;

.form{
    margin-top: 5rem;
    width: 100%;
}

.field{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label{
        flex: 0 0 10rem;
    }
    @include m.tablet{
        label{
            display: none;
        }
    }
    input{
        flex: 1;
        border: none;
        padding: calc(v.$separacion/4);
        border-radius: 1rem;

        &:disabled{
            background-color: lighten(v.$negro, 30%);
            color: v.$blanco;
            cursor: not-allowed;
        }
    }
}